import React, { useState, useCallback, useEffect } from 'react';
import Cropper from 'react-easy-crop';
import getCroppedImg from './getCroppedImg';
import './App.css';

function App() {
  const [imageSrc, setImageSrc] = useState(null); // 선택된 이미지 소스
  const [fileName, setFileName] = useState(null);
  const [orientation, setOrientation] = useState('vertical'); // 가로/세로 모드
  const [crop, setCrop] = useState({ x: 0, y: 0 }); // 크롭 위치
  const [zoom, setZoom] = useState(1); // 확대/축소
  const [aspect, setAspect] = useState(400 / 600); // 기본 세로 모드의 가로세로 비율
  const [croppedAreaPixels, setCroppedAreaPixels] = useState(null); // 크롭된 영역의 픽셀 정보
  const [croppedImage, setCroppedImage] = useState(null); // 최종 크롭된 이미지

  // 파일 선택 시 호출되는 함수
  const onFileChange = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      const reader = new FileReader();
      reader.addEventListener('load', () => setImageSrc(reader.result));
      reader.readAsDataURL(e.target.files[0]);
      // console.log();
      const fileNameWithExt = e.target.files[0].name; // 전체 파일 이름 (예: example.jpg)
      const fileName = fileNameWithExt.substring(0, fileNameWithExt.lastIndexOf('.')) || fileNameWithExt;
      setFileName(fileName);
    }
  };

  // 가로/세로 모드 변경 시 호출되는 함수
  const onOrientationChange = (e) => {
    const value = e.target.value;
    setOrientation(value);
    setAspect(value === 'vertical' ? 400 / 600 : 600 / 400);
  };

  // 크롭 완료 시 호출되는 함수
  const onCropComplete = useCallback((croppedArea, croppedAreaPixels) => {
    setCroppedAreaPixels(croppedAreaPixels);
  }, []);

  // 크롭된 이미지를 생성하는 함수
  const generateCroppedImage = useCallback(async () => {
    if (!croppedAreaPixels || !imageSrc) return;
    try {
      const outputSize =
        orientation === 'vertical'
          ? { width: 400, height: 600 }
          : { width: 600, height: 400 };

      const croppedImage = await getCroppedImg(
        imageSrc,
        croppedAreaPixels,
        outputSize
      );
      setCroppedImage(croppedImage);
    } catch (e) {
      console.error(e);
    }
  }, [croppedAreaPixels, imageSrc, orientation]);

  useEffect(() => {
    generateCroppedImage();
  }, [generateCroppedImage]);

  // 업로드 버튼 클릭 시 호출되는 함수
  const onUpload = async () => {
    // croppedImage를 서버로 업로드하거나 필요한 처리를 합니다.

    if (!croppedImage) {
      alert('이미지를 먼저 크롭하세요.');
      return;
    }
  
    try {
      const rescrop = await fetch(croppedImage);
      const blob = await rescrop.blob();

      const formData = new FormData();
      formData.append('image', blob, fileName + '.png');
  
      const response = await fetch('/upload', {
        method: 'POST',
        body: formData,
      });
  
      if (response.ok) {
        alert('이미지가 성공적으로 업로드되었습니다.');
      } else {
        alert('이미지 업로드 실패: ' + response.statusText);
      }
    } catch (error) {
      console.error('이미지 업로드 중 오류 발생:', error);
      alert('이미지 업로드 실패');
    }
  };

  return (
    <div className="container">
      <h1>이미지 업로더</h1>

      <input type="file" accept="image/*" onChange={onFileChange} />

      <div className="orientation-selector">
        <label>
          <input
            type="radio"
            value="vertical"
            checked={orientation === 'vertical'}
            onChange={onOrientationChange}
          />
          세로
        </label>
        <label>
          <input
            type="radio"
            value="horizontal"
            checked={orientation === 'horizontal'}
            onChange={onOrientationChange}
          />
          가로
        </label>
      </div>

      <div className="editor-preview">
        <div className="editor">
          {imageSrc ? (
            <Cropper
              image={imageSrc}
              crop={crop}
              zoom={zoom}
              minZoom={0.6}
              aspect={aspect}
              onCropChange={setCrop}
              onZoomChange={setZoom}
              onCropComplete={onCropComplete}
              showGrid={true}
              cropShape="rect"
              restrictPosition={false}
              classes={{ cropAreaClassName: 'crop-area' }}
            />
          ) : (
            <div className="placeholder">이미지를 선택하세요.</div>
          )}
        </div>

        <div className="preview">
          {croppedImage ? (
            <img src={croppedImage} alt="Preview" />
          ) : (
            <div className="placeholder">프리뷰 이미지</div>
          )}
        </div>
      </div>

      <div className="controls">
        <label>확대/축소</label>
        <input
          type="range"
          min={0.2}
          max={3}
          step={0.1}
          value={zoom}
          onChange={(e) => setZoom(e.target.value)}
        />
      </div>

      <button onClick={onUpload}>업로드</button>
    </div>
  );
}

export default App;
